export default {
  name: "skeleton",
  props: {
    bgcolor: {
      type: String,
      value: "#FFF"
    },
    selector: {
      type: String,
      value: "skeleton"
    },
    loading: {
      type: String,
      value: "spin"
    }
  },
  data() {
    return {
      loadingAni: ["spin", "chiaroscuro"],
      systemInfo: {},
      skeletonRectLists: [],
      skeletonCircleLists: []
    };
  },
  methods: {
    attachedAction() {
      //默认的首屏宽高，防止内容闪现
      const systemInfo = uni.getSystemInfoSync();
      this.systemInfo = {
        width: systemInfo.windowWidth,
        height: systemInfo.windowHeight
      };
      this.loading = this.loadingAni.includes(this.loading)
        ? this.loading
        : "spin";
    },
    readyAction() {
      const that = this;
      //绘制背景
      uni
        .createSelectorQuery()
        .selectAll(`.${this.selector}`)
        .boundingClientRect()
        .exec(function(res) {
          that.systemInfo.height = res[0][0].height + res[0][0].top;
        });

      //绘制矩形
      this.rectHandle();

      //绘制圆形
      this.radiusHandle();
    },
    rectHandle() {
      const that = this;

      //绘制不带样式的节点
      uni
        .createSelectorQuery()
        .selectAll(`.${this.selector}-rect`)
        .boundingClientRect()
        .exec(function(res) {
          that.skeletonRectLists = res[0];
        });
    },
    radiusHandle() {
      const that = this;

      uni
        .createSelectorQuery()
        .selectAll(`.${this.selector}-radius`)
        .boundingClientRect()
        .exec(function(res) {
          that.skeletonCircleLists = res[0];
        });
    }
  }
};
